@import url(https://fonts.googleapis.com/css?family=Pacifico);
@import url(https://fonts.googleapis.com/css?family=Comfortaa);
@import url(https://fonts.googleapis.com/css?family=Cookie);
@import url(https://fonts.googleapis.com/css?family=Arvo);
.s-font-wish-heading {
  font-family: 'Pacifico', cursive; }

.s-font-wish-heading p {
  margin: 25px;
  font-size: 130%; }

.s-font-name-heading {
  font-family: 'Comfortaa', cursive;
  font-size: 40px;
  margin-bottom: 0px;
  margin-top: 2px; }

.s-font-role-heading {
  font-family: 'Comfortaa', cursive;
  font-size: 24px;
  margin: 0px; }

.d--grid--name {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(33.333%, auto));
  grid-template-rows: 1fr; }

.d--grid {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(33.333%, auto));
  grid-template-rows: repeat(4, minmax(2rem, auto));
  grid-template-areas: "profile profile profile profile" "profile profile profile profile" "profile techSkils projects projects"; }

.slash--heading {
  padding-bottom: 20px;
  border-bottom: 1px solid var(--border-color);
  border-bottom-width: 2px; }

.icon--row, h5, p {
  grid-column: 1/-1;
  margin-bottom: 4px;
  margin-top: 0px; }

.profile--row--col {
  grid-column: 1/2;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100%, auto));
  grid-template-rows: auto;
  margin-left: 20px;
  border-right: 1px solid var(--border-color); }

.profile--row--col div {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-self: center; }

.profile--row--col div.circle--badge {
  display: grid;
  grid-template-columns: 90px;
  grid-template-rows: 1fr;
  justify-self: center;
  -webkit-align-items: end;
          align-items: end;
  margin-bottom: 1rem; }

.linked-in-btn {
  grid-column: 1/-1;
  justify-self: start; }

h2 {
  margin: 0px; }

.technical--skills--row--col {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 4px;
  padding: 4px; }

.experience--row--col {
  margin: 0px;
  box-shadow: none;
  border-top: none;
  border-left: none;
  border-bottom: none; }

.entity {
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.link-color {
  background-color: #039be5 !important;
  color: #fff; }

.sub-label {
  color: var(--border-color); }

/* Landscape */
@media only screen and (min-width: 320px) and (max-width: 850px) {
  .profile--row--col, .technical--skills--row--col, .experience--row--col {
    grid-column: 1/-1;
    border: 0px; } }

/* Landscape */
@media only screen and (min-width: 850px) and (max-width: 1080px) {
  .d--grid {
    grid-template-areas: "profile profile profile profile" "profile profile profile profile" "profile projects  techSkils techSkils"; }
  .profile--row--col {
    grid-column: 1/3;
    border: 0px; }
  .technical--skills--row--col {
    grid-column: 1/-1;
    border: 0px; }
  .experience--row--col {
    grid-column: 3/-1; } }

.interviewSection {
  background-image: url(/profile/static/media/thimble-pattern@2x.5b6d1d40.png);
  background-repeat: repeat repeat;
  background-size: 200px;
  background-color: #47ad66;
  color: #ffffff; }

#interviewComponent {
  background-image: url(/profile/static/media/interview_preparation.d2c37cd5.png);
  height: 25rem;
  background-size: 46rem;
  background-position: inherit;
  background-repeat: no-repeat; }

.interview-card {
  display: grid;
  grid-template-columns: 1fr;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

.interview-card-content {
  display: grid;
  grid-template-columns: 1fr;
  -webkit-justify-content: center;
          justify-content: center;
  height: 22rem;
  text-align: center; }

.interviewQuote {
  display: grid;
  grid-template-columns: 96%;
  -webkit-justify-content: center;
          justify-content: center; }

.separator {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  border-bottom: 3px solid var(--border-color);
  margin-top: 1rem;
  margin-bottom: 2rem; }

.tbl-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
  border-bottom: 1px solid var(--border-color); }

/*** 
*
* Used for TweakWorkTemplate
*
*/
.d--grid--TweakWorkTemplate {
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(auto, auto)); */
  grid-template-rows: 1fr; }

.d--grid--TweakWorkTemplate-labels {
  -webkit-align-items: center;
          align-items: center;
  display: grid; }

.div-border-bottom {
  border-bottom: 1px solid var(--border-color); }

/**** End of TweakWorkTemplate ****/

.d--grid--2--columns--wrap {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(50%, auto)); }

.self-dark-blue {
  background-color: #306584 !important; }

.adminLayout {
  display: grid;
  grid-template-rows: 50px;
  grid-template-columns: 1fr;
  height: 100vh; }

.adminLayout .contentSpace {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10rem, auto)); }

#loginContainer {
  display: grid;
  grid-template-columns: 40rem;
  grid-template-rows: 1fr;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

:root {
  --black: #272727;
  --border-color: #bbb;
  --new-work-studio-log-color: #2B3252; }

html {
  font-family: GillSans, Calibri, Trebuchet, sans-serif; }

html, body, .block {
  height: 100%; }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #F5F5F5;
  background-color: #F5F5F5; }

html::-webkit-scrollbar {
  width: 6px;
  background-color: #F5F5F5; }

html::-webkit-scrollbar-thumb {
  background-color: #306584;
  border: 1px solid #306584; }

.cursor-pointer {
  cursor: pointer !important; }

.cursor-pointer:hover {
  color: #2B3252;
  color: var(--new-work-studio-log-color); }

.tbl-d-grid {
  display: grid;
  grid-template-columns: repeat(6, minmax(2rem, auto)); }

.tbl-d-grid > span.heading {
  color: #2B3252;
  color: var(--new-work-studio-log-color);
  display: grid;
  -webkit-justify-content: center;
          justify-content: center; }

.tbl-d-grid > span {
  padding: 8px 4px;
  word-wrap: break-word; }

.nav-bg-color {
  background-color: #EF5455;
  color: #bbb;
  color: var(--border-color); }

.logo {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 12px;
  width: 50rem;
  height: 4.2rem;
  display: -webkit-flex;
  display: flex;
  /* grid-template-columns: repeat(auto-fit, minmax(2px, auto)); */
  -webkit-align-items: flex-start;
          align-items: flex-start; }

.hoverable a {
  text-decoration: none; }

.hoverable a:hover {
  text-decoration: none;
  color: #2B3252 !important;
  color: var(--new-work-studio-log-color) !important; }

.logo a:first-child {
  font-family: 'Arvo', serif;
  font-size: 20px;
  width: auto;
  padding-left: 30px;
  padding-right: 5px;
  color: #2B3252 !important;
  color: var(--new-work-studio-log-color) !important; }

.logo a:first-child:hover {
  text-decoration: none;
  color: #2B3252 !important;
  color: var(--new-work-studio-log-color) !important; }

.owner-name-display {
  font-family: 'Cookie', cursive;
  color: #FAD744;
  font-size: 40px; }

.toastClass {
  background-color: #EF5455;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 16px; }

.templateSection {
  min-height: 10rem;
  max-height: 10rem;
  overflow-y: auto; }

.templateSection::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #F5F5F5;
  background-color: #F5F5F5; }

.templateSection::-webkit-scrollbar {
  width: 6px;
  background-color: #F5F5F5; }

.templateSection::-webkit-scrollbar-thumb {
  background-color: #306584;
  border: 1px solid #306584; }

.row {
  margin: 0px !important; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
